import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ModalController, ToastController } from '@ionic/angular';
import { ApiService } from 'src/app/services/api.service';
import { CommonService } from 'src/app/services/common.service';

@Component({
  selector: 'app-add-tenant',
  templateUrl: './add-tenant.page.html',
  styleUrls: ['./add-tenant.page.scss'],
})
export class AddTenantPage implements OnInit {
  submitted = false;
  tenant = {
    name: null,
    room: null,
    phone: null,
    property: null,
    occupants: null,
    role: {
      id: 't',
      description: 'Tenant',
    },
    hasCar: false,
  };
  tenants = [];
  user;
  role;
  menuItems = [
    {
      icon: 'home',
      name: 'Home',
      routerLink: './home',
    },
  ];
  constructor(
    private common: CommonService,
    private router: Router,
    private api: ApiService,
    public modalController: ModalController,
    private toastController: ToastController
  ) {}
  ngOnInit() {
    this.role = JSON.parse(localStorage.getItem('role'));
    this.user = this.common.getUser();
    this.transformMenu();
  }

  ionViewDidEnter() {
    this.role = JSON.parse(localStorage.getItem('role'));
    this.user = this.common.getUser();
    if (this.menuItems.length <= 1) {
      this.role = JSON.parse(localStorage.getItem('role'));
      this.user = this.common.getUser();
      this.transformMenu();
    } else if (this.user !== null) {
      console.log(this.user);
      this.router.navigate(['landing']);
    } else if (this.user === null) {
      localStorage.clear();
      this.router.navigate(['landing']);
    } else {
      localStorage.clear();
      this.router.navigate(['home']);
    }
  }

  ionViewWillLeave() {
    this.menuItems = [
      {
        icon: 'home',
        name: 'Home',
        routerLink: './home',
      },
    ];
    console.log('reset nav');
  }

  logout() {
    localStorage.removeItem('user');
    this.router.navigate(['./home']);
    window.location.reload();
  }
  transformMenu() {
    if (
      JSON.parse(localStorage.getItem('user')) === undefined ||
      JSON.parse(localStorage.getItem('user')) === null
    ) {
      console.log('Guest');
      this.menuItems.splice(
        1,
        0,
        {
          icon: 'apartment',
          name: 'Properties',
          routerLink: './properties',
        },
        {
          icon: 'chat',
          name: 'Contact',
          routerLink: './contact',
        }
      );
    } else if (
      this.user !== null &&
      this.user !== undefined &&
      (this.user.role.id === 'a' || this.user.role.id === 'l')
    ) {
      console.log('admin and landlord con');
      this.menuItems.splice(
        1,
        0,
        {
          icon: 'account_circle',
          name: 'Profile',
          routerLink: './profile',
        },
        {
          icon: 'people',
          name: 'Tenants',
          routerLink: './tenants',
        },
        {
          icon: 'help_center',
          name: 'Queries',
          routerLink: './queries',
        },
        {
          icon: 'apartment',
          name: 'Properties',
          routerLink: './properties',
        },
        {
          icon: 'power_settings_new',
          name: 'Logout',
          routerLink: '',
        }
      );
    } else if (
      this.user !== null &&
      this.user !== undefined &&
      this.user.role.id === 't'
    ) {
      console.log('tenant con');
      this.menuItems.splice(
        1,
        0,
        {
          icon: 'help_center',
          name: 'Queries',
          routerLink: './queries',
        },
        {
          icon: 'apartment',
          name: 'Properties',
          routerLink: './properties',
        },
        {
          icon: 'chat',
          name: 'Contact',
          routerLink: './contact',
        },
        {
          icon: 'power_settings_new',
          name: 'Logout',
          routerLink: './queries',
        }
      );
    } else {
      console.log('team');
      this.menuItems.splice(
        1,
        0,
        {
          icon: 'account_circle',
          name: 'Profile',
          routerLink: './profile',
        },
        {
          icon: 'help_center',
          name: 'Queries',
          routerLink: './queries',
        },
        {
          icon: 'power_settings_new',
          name: 'Logout',
          routerLink: '',
        }
      );
    }
  }
  checkRoute(route) {
    if (route.name === 'Logout') {
      localStorage.clear();
      this.router.navigate(['./home']);
    } else {
      this.router.navigate([route.routerLink]);
    }
  }
  async presentToast(msg, toastClass) {
    await this.toastController
      .create({
        message: msg,
        cssClass: toastClass,
        duration: 2000,
      })
      .then((res) => res.present());
  }

  addNewTenant(form) {
    this.submitted = true;
    if (form.invalid) {
      this.presentToast('Please fill in all fields!', 'failed');
      return false;
    } else {
      form.value.role = this.tenant.role;
      console.log(form.value);
      this.api.addTenant('tenant/addTenant', form.value).subscribe((res) => {
        if (!res?.err) {
          this.presentToast('Tenant Added Successfully', 'success');
          setTimeout(() => {
            this.router.navigate(['./tenants']);
          }, 2200);
        } else {
          this.presentToast(res?.err, 'failed');
        }
      });
    }
  }
}
